import toastr from 'toastr'

function property(position) {

    switch (position) {
        case 'top-left':
            position = 'toast-top-left'
            break
        case 'top-middle':
            position = 'toast-top-center'
            break
        case 'top-right':
            position = 'toast-top-right'
            break
        case 'bottom-left':
            position = 'toast-bottom-left'
            break
        case 'bottom-middle':
            position = 'toast-bottom-center'
            break
        case 'bottom-right':
            position = 'toast-bottom-right'
            break
        default:
            position = 'toast-top-right'
            break
    }

    return {
        closeButton: true,
        debug: false,
        newestOnTop: true,
        progressBar: false,
        positionClass: position,
        preventDuplicates: false,
        onclick: null,
        showDuration: "300",
        hideDuration: "1000",
        timeOut: "5000",
        extendedTimeOut: "1000",
        showEasing: "swing",
        hideEasing: "linear",
        showMethod: "fadeIn",
        hideMethod: "fadeOut"
    }
}

function toast(action, message, title, position) {
    switch (action) {
        case 'success':
            toastr.success(message, title, property(position))
            break
        case 'info':
            toastr.info(message, title, property(position))
            break
        case 'warning':
            toastr.warning(message, title, property(position))
            break
        case 'error':
            toastr.error(message, title, property(position))
            break
        default:
            break
    }
}

export default toast