import React from 'react'
import { Route, Redirect, Switch } from 'react-router-dom'
import IndexRoutes from '../Routes';
import Loading from './loading';
import Sidebar from './sidebar/sidebar'

const Layout = (props) => {
    const children = IndexRoutes.map((route, idx) => {
        return route.component ? (
            <Route
                key={idx}
                path={route.path}
                exact={route.exact}
                name={route.name}
                render={(props) => <route.component {...props} />}
            />
        ) : null;
    })

    return (
        <>
            <div className="container-fluid" style={{ maxWidth: '1500px', }}>
                <div className="row" >
                    <div className="col-sm-2" style={{ marginTop: '50px' }}>
                        <Sidebar { ...props}/>
                    </div>
                    <div className="col-sm-10  main-content " style={{ marginTop: '50px' }}>
                        <React.Suspense fallback={<Loading/>}>
                            <Switch>
                                <Route exact path="/">
                                    <Redirect to="/backend" />
                                </Route>
                                {children}
                            </Switch>
                        </React.Suspense>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Layout