import axios from 'axios'
import toast from "../../Component/toastr"
import "toastr/build/toastr.min.css"

const URL = `${process.env.REACT_APP_BASE_URL}/api/subscribers`;
const URL_POST = `${process.env.REACT_APP_BASE_URL}/api/subscriber`;

const request = axios.create({
    baseURL: URL,
});

const LoadSubscriberSuccess = (datas) => ({
    type: 'LOAD_SUBSCRIBER_SUCCESS',
    datas,
})
const isLoading = (data) => ({
    type: 'SUBSCRIBER_LOADING',
    data,
});
const LoadSearchSubscriberSuccess = (datas) => ({
    type: 'LOAD_SUBSCRIBER_SEARCH_SUCCESS',
    datas,
})
const postSubscriberSuccess = (datas) => ({
    type: 'POST_SUBSCRIBER_SUCCESS',
    datas,
})
const putSubscriberSuccess = (datas) => ({
    type: 'EDIT_SUBSCRIBER_SUCCESS',
    datas,
})
const deleteSubscriberSuccess = (datas) => ({
    type: 'DELETE_SUBSCRIBER_SUCCESS',
    datas,
})
export const loadSubscriber = () => {
    return (dispatch) => {
        dispatch(isLoading(true))
        return request.get(URL, {})
            .then(function (response) {
                dispatch(LoadSubscriberSuccess(response.data.data.subscribers));
            })
            .catch(function (error) {
                console.error(error);
            })
            .finally(() => {
                setTimeout(function () { dispatch(isLoading(false)); }, 1000);
            });
    };
};
export const loadSearchSubscriber = (param) => {
    return (dispatch) => {
        return request.get(`${process.env.REACT_APP_BASE_URL}/api/subscribers?q=${param}`, {})
            .then(function (response) {
                dispatch(LoadSearchSubscriberSuccess(response.data.data.subscribers));
            })
            .catch(function (error) {
                console.error(error);
            });
    };
};
export const postSubscriber = (eventId, beId, queue, action) => {
    const payload = {
        _event_id: eventId,
        _backend_subscriber_id: beId,
        queue: queue,
        post_action: action
    }
    return (dispatch) => {
        return request.post(URL_POST, payload)
            .then(function (response) {
                dispatch(postSubscriberSuccess(response.data.data.events));
                if (response) {
                    toast("success", "Subscriber Berhasil Ditambahkan", "", "")
                    setTimeout(() => {
                        window.location.reload()
                    }, 2000);
                }
            })
            .catch(function (error) {
                console.error(error);
                if (error.response.status === 400) {
                    toast("error", "Bad Request", "Data Salah/Tidak Ditemukan", "")
                    setTimeout(() => {
                        window.location.reload()
                    }, 2000);
                }
                else if (error.response.status === 500) {
                    toast("error", "Internal Server Error", "Something Wrong", "")
                    setTimeout(() => {
                        window.location.reload()
                    }, 2000);
                }
                else if (error.response.status === 403) {
                    toast("error", "Acces Forbidden", "FOrbidden Access", "")
                    setTimeout(() => {
                        window.location.reload()
                    }, 2000);
                }
            });
    };
};
