import { useState } from 'react'
import { useDispatch } from 'react-redux'
import { deleteBackend, putBackend } from '../../Redux/Backend/actions'
import Modals from '../modals'

const BackendList = (props) => {
    const dispatch = useDispatch()
    const [isOpen] = useState(false)
    const [name, setName] = useState(props.name)
    const [url, setUrl] = useState(props.url)
    const [title, setTitle] = useState("")
    const [body, setBody] = useState("")
    const onChangeName = (e) => {
        setName(e.target.value)
    }
    const onChangeUrl = (e) => {
        setUrl(e.target.value)
    }
    const add_body = () => {
        return (
            <>
                <form>
                    <div className="row mb-3">
                        <label htmlFor="name" className="col-sm-2 col-form-label">Name <span style={{ color: 'red' }}>*</span></label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" id="inputEmail3" defaultValue={name} onChange={onChangeName} />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label htmlFor="url" className="col-sm-2 col-form-label">Url <span style={{ color: 'red' }}>*</span></label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" id="inputPassword3" defaultValue={url} onChange={onChangeUrl} />
                        </div>
                    </div>
                </form>

            </>
        )
    }
    const onClickModal = () => {
        setTitle("Edit Backend")
        setBody(add_body)
    }
    const onClickDelete = () => {
        alert('Apakah Anda Yakin ?')
        dispatch(deleteBackend(props.id))
    }

    const onSubmit = () => {
        alert('Apakah Anda Yakin ?')
        dispatch(putBackend(name, url, props.id))
    }
    return (
        <>
            <tr style={{ textAlign: 'left' }} id={props.id} key={props.idx.toString()} >
                <th scope="row">{props.idx + 1}</th>
                <td>{props.name}</td>
                <td className="text-center"><a href={`/event/exc/${props.name}`} style={{textDecoration:'none'}}>{props.total_exchanges}</a></td>
                <td className="text-center"><a href={`/subscribers/queue/${props.name}`} style={{ textDecoration: 'none' }}>{props.total_queues}</a></td>
                <td>
                    <a href={props.url} target="_blank" rel="nofollow" title="tes">
                        <i className="fa fa-eye" aria-hidden="true" style={{ marginRight: '10px', cursor: 'pointer' }}></i>
                    </a>
                    <i className="fa fa-pencil-square-o" aria-hidden="true" style={{ marginRight: '10px', cursor: 'pointer' }} style={{ marginRight: '10px', cursor: 'pointer' }}
                        data-bs-toggle="modal" data-bs-target={`#exampleModal${props.id}`} onClick={onClickModal}></i>
                    <Modals
                        id={props.id}
                        isOpen={isOpen}
                        title={title}
                        body={body}
                        onSubmit={onSubmit} />
                    <i className="fa fa-trash-o" aria-hidden="true" style={{ marginRight: '10px', cursor: 'pointer' }} onClick={onClickDelete}></i>
                </td>
            </tr>
           
        </>
    )
}

export default BackendList