import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Loading from '../Component/loading'
import Modals from '../Component/modals'
import SubscriberList from '../Component/subscriber/subscriberlist'
import { loadBackend } from '../Redux/Backend/actions'
import { loadEvent } from '../Redux/Events/actions'
import { loadSearchSubscriber, loadSubscriber, postSubscriber } from '../Redux/Subscriber/actions'

const Subscriber = () => {
    const abc = window.location.href;
    const def = abc.trim();
    const dataQueue = def.split("/")[5];

    const dispatch = useDispatch()

    const data = useSelector((state) => state.subscriber.dataSubscriber);
    const loading = useSelector((state) => state.subscriber.isLoading)
    const dataSearch = useSelector((state) => state.subscriber.dataSearch);
    const dataBE = useSelector((state) => state.backend.dataBackend)
    const dataEvent = useSelector((state) => state.events.dataEvent);

    const [queue, setQueue] = useState("")
    const [event, setEvent] = useState("")
    const [bename, setBename] = useState("")
    const [postaction, setPostaction] = useState("")
    const [title, setTitle] = useState("")
    const [body, setBody] = useState("")
    const [id, ] = useState("addsubscriber")
    const [search, setSearch] = useState("")
    const [updateSearch, setUpdateSearch] = useState("")

    const onChangeQueue = (e) => {
        setQueue(e.target.value)
    }
    const onChangeEvent = (e) => {
        setEvent(e.target.value)
    }
    const onChangeBename = (e) => {
        setBename(e.target.value)
    }
    const onChangePostaction = (e) => {
        setPostaction(e.target.value)
    }

    const handleSearchChange = (e) => {
        setUpdateSearch(e.target.value)
    }

    useEffect(() => {
        dispatch(loadSubscriber())
        dispatch(loadEvent())
        dispatch(loadBackend())
        if (dataQueue) {
            dispatch(loadSearchSubscriber(dataQueue))
        }
    }, [dataQueue])

    useEffect(() => {
        if (dataQueue) {
            setSearch(
                dataSearch && dataSearch.map((item, idx) => {
                    return (
                        <>
                            <SubscriberList
                                idx={idx + 1}
                                id={item._id}
                                exchange={item._exchange}
                                eventid={item._event_id}
                                queue={item.queue}
                                postaction={item.post_action}
                                subscribername={item._backend_subscriber_name}
                                subscriberid={item._backend_subscriber_id}
                                dataBE={dataBE}
                                dataEvent={dataEvent}
                            />
                        </>
                    )
                })
            )
        }

    }, [dataQueue, dataSearch, dataBE, dataEvent])


    const dataNew = data.filter(
        (item) =>
            item._exchange.toLowerCase().includes(updateSearch) ||
            item._backend_subscriber_name.toLowerCase().includes(updateSearch) ||
            item.queue.toLowerCase().includes(updateSearch)
    )

    const list = dataNew.map((item, idx) => {
        return (
            <>
                <SubscriberList
                    idx={idx + 1}
                    id={item._id}
                    exchange={item._exchange}
                    eventid={item._event_id}
                    queue={item.queue}
                    postaction={item.post_action}
                    subscribername={item._backend_subscriber_name}
                    subscriberid={item._backend_subscriber_id}
                    dataBE={dataBE}
                    dataEvent={dataEvent}
                />

            </>
        )
    })

    const add_body = () => {
        return (
            <>
                <form>
                    <div className="mb-3" style={{ textAlign: 'left' }}>
                        <label htmlFor="queue" className="form-label">Queue <span style={{ color: 'red' }}>*</span></label>
                        <input type="text" className="form-control" id="inputEmail3" defaultValue={queue} onChange={onChangeQueue} placeholder="Input Queue"/>
                    </div>

                    <div className="row">
                        <div className="col">
                            <div className="mb-3" style={{ textAlign: 'left' }}>
                                <label htmlFor="event" className="form-label">Event / Exchange <span style={{ color: 'red' }}>*</span></label>
                                <select class="form-select" aria-label="Default select example" onChange={onChangeEvent}>
                                    {event ? (
                                        <option selected>{event}</option>
                                    ) :
                                        (<option disabled selected>--Pilih--</option>)}
                                    {dataEvent.map((item, idx) => {
                                        return (
                                            <option value={item.id} key={item.id}>{item.exchange}</option>
                                        )
                                    })}
                                </select>
                            </div>
                        </div>

                        <div className="col">
                            <div className="mb-3" style={{ textAlign: 'left' }}>
                                <label htmlFor="bename" className="form-label">Subcriber Backend <span style={{ color: 'red' }}>*</span></label>
                                <select class="form-select" aria-label="Default select example" onChange={onChangeBename}>
                                    {bename ? (
                                        <option selected>{bename}</option>
                                    ) :
                                        (<option disabled selected>--Pilih--</option>)}
                                    {dataBE.map((item, idx) => {
                                        return (
                                            <option value={item.id} key={item.id}>{item.name}</option>
                                        )
                                    })}
                                </select>
                            </div>
                        </div>
                    </div>

                    <div className="mb-3" style={{ textAlign: 'left' }}>
                        <label htmlFor="post_action" className="form-label">Post Action </label>
                        <input type="text" className="form-control" id="inputEmail3" defaultValue={postaction} onChange={onChangePostaction} placeholder="Input Post Action"/>
                    </div>
                </form>

            </>
        )
    }
    const onClickAddModal = () => {
        setTitle("Create Subscriber / Queue")
        setBody(add_body)
    }
    const onSubmit = (e) => {
        e.preventDefault() 
        if (event === "") {
            alert("Event harus diisi!");
        }
        if (bename === "") {
            alert("Backend name harus diisi!");
        }
        if (queue === "") {
            alert("Queue harus iisi!");
        } else {
            dispatch(postSubscriber(event, bename, queue, postaction))
        }
      
    }
    if (loading) {
        return (
            <Loading />
        );
    }
    return (
        <>

            <div style={{ display: 'flex' }}>
                <div className="col-6">
                    <h4 className="mb-2" style={{ textAlign: 'left' }}>List Subscriber/Queue</h4>
                </div>
                <div className="col-6" style={{ margin: 'auto', textAlign: 'right' }}>
                    <button type="button" className="btn btn-primary"
                        data-bs-toggle="modal" data-bs-target={`#exampleModal${id}`} onClick={onClickAddModal} style={{ backgroundColor: '#30A4E1' }}>Add Subscriber</button>
                </div>
            </div>
            <hr></hr>
            <div className="mb-3">
                <input
                    type="text"
                    className="form-control"
                    placeholder="Search"
                    onChange={handleSearchChange}
                />
            </div>

            <div className="table-responsive">
                <table className="table table-striped table-hover">
                    <thead>
                        <tr style={{ textAlign: 'left' }}>
                            <th scope="col">#</th>
                            <th scope="col">Exchange</th>
                            <th scope="col">Queue Name</th>
                            <th scope="col">Backend Name</th>
                            <th scope="col">Actions</th>

                        </tr>
                    </thead>
                    <tbody>
                        {dataQueue !== undefined ? search : list}
                    </tbody>
                </table>
            </div>
            <Modals
                id={id}
                title={title}
                body={body}
                onSubmit={onSubmit}
            />
        </>
    )
}

export default Subscriber