import React from 'react'

const Modals = (props) => {
    const { title, body, id, onSubmit } = props;
    return (
        <>
            <div className="modal fade" id={`exampleModal${id}`} tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered modal-lg">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="exampleModalLabel">{title}</h5>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div className="modal-body">
                            {body}
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-primary" onClick={onSubmit} style={{ backgroundColor: '#30A4E1' }}>Submit</button>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Modals