import { useState } from 'react'
import { useDispatch } from 'react-redux'
import { deleteEvent, putEvent } from '../../Redux/Events/actions'
import Modals from '../modals'

const EventsList = (props) => {
    const dispatch = useDispatch()
    const [bename, setBeName] = useState(props.pubId)
    const [benameField, ] = useState(props.publisher)

    const [model, setModel] =useState(props.model_id)
    const [modelField, ] = useState(props.model_name)
    const [exchange, setExchange] = useState(props.exchange)
    const [trigger, setTrigger] = useState(props.trigger)
    const [endpoint, setEndpoint] = useState(props.endpoint)
    const [description, setDescription] = useState(props.description)
    const [sample_message, setSampleMessage] = useState(props.sample_message)
    const [title, setTitle] = useState("")
    const [body, setBody] = useState("")

    const onChangeBename = (e) => {
        setBeName(e.target.value)
    }
    const onChangeModel = (e) => {
        setModel(e.target.value)
    }
    const onChangeExchange = (e) => {
        setExchange(e.target.value)
    }
    const onChangeTrigger = (e) => {
        setTrigger(e.target.value)
    }
    const onChangeEndpoint = (e) => {
        setEndpoint(e.target.value)
    }
    const onChangeDescription = (e) => {
        setDescription(e.target.value)
    }
    const onChangeSampleMessage = (e) => {
        setSampleMessage(e.target.value)
    }
    const add_body = () => {
        return (
            <>
                <form>
                    <div className="mb-3" style={{textAlign:'left'}}>
                        <label htmlFor="exchange" className="form-label">Exchange <span style={{ color: 'red' }}>*</span></label>
                            <input type="text" className="form-control" id="inputEmail3" defaultValue={exchange} onChange={onChangeExchange} />
                    </div>

                    <div className="row">
                        <div className="col">
                            <div className="mb-3" style={{ textAlign: 'left' }}>
                                <label htmlFor="backend" className="form-label">Backend Name <span style={{ color: 'red' }}>*</span></label>
                                    <select className="form-select" aria-label="Default select example" onChange={onChangeBename}>
                                        {bename ? (
                                        <option defaultValue={bename} >{benameField}</option>
                                        ) :
                                        (<option disabled value="" selected>-Pilih-</option>) }
                                        {props.dataBE.map((item, idx) => {
                                            return (
                                                <option defaultValue={item.id} key={item.id} value={item.id}>{item.name}</option>
                                            )
                                        })}
                                    </select>
                            </div>
                        </div>
                        <div className="col">
                            <div className="mb-3" style={{ textAlign: 'left' }}>
                                <label htmlFor="model" className="form-label">Model <span style={{ color: 'red' }}>*</span></label>
                                    <select className="form-select" aria-label="Default select example"onChange={onChangeModel}>
                                    {model ? (
                                        <option defaultValue={model} >{modelField}</option>
                                    ) :
                                        (<option disabled value="" selected>-Pilih-</option>)}
                                        {props.dataMdl.map((item, idx) => {
                                            console.log(item.id)
                                            return (
                                                <option defaultValue={item.id} key={item.id} value={item.id}>{item.name}</option>
                                            )
                                        })}
                                        </select>
                            </div>
                        </div>
                    </div>        

                    <div className="row">
                        <div className="col">
                            <div className="mb-3" style={{ textAlign: 'left' }}>
                                <label htmlFor="triger" className="form-label">Triger Action</label>
                                <input type="text" className="form-control" id="inputEmail3" defaultValue={trigger} onChange={onChangeTrigger} />
                            </div>
                        </div>

                        <div className="col">
                            <div className="mb-3" style={{ textAlign: 'left' }}>
                                <label htmlFor="endpoint" className="form-label">Endpoint</label>
                                <input type="text" className="form-control" id="exampleFormControlInput1" defaultValue={endpoint} placeholder="input Url"
                                onChange={onChangeEndpoint} />
                            </div>
                        </div>
                    </div>              
                    
                    <div className="row">
                        <div className="col">
                            <div className="mb-3" style={{ textAlign: 'left' }}>
                                <label htmlFor="sample_message" className="form-label">Sample JSON Message</label>
                                <textarea className="form-control" id="exampleFormControlTextarea1" defaultValue={sample_message} rows="8" onChange={onChangeSampleMessage}></textarea>
                            </div>
                        </div>

                        <div className="col">
                            <div className="mb-3" style={{ textAlign: 'left' }}>
                                <label htmlFor="description" className="form-label">Description</label>
                                <textarea className="form-control" id="exampleFormControlTextarea1" defaultValue={description} rows="8" onChange={onChangeDescription}></textarea>
                            </div>
                        </div>
                    </div>
                </form>

            </>
        )
    }
    const onClickModal = () => {
        setTitle("Edit Event")
        setBody(add_body)
    }
    const onClickDelete = () => {
        alert('Apakah Anda Yakin ?')
        dispatch(deleteEvent(props.id))
    }

    const onSubmit = () => {
        alert('Apakah Anda Yakin ?')
        dispatch(putEvent(bename, model, exchange, trigger, endpoint, description, sample_message, props.id))
    }
    return (
        <>
            <tr style={{ textAlign: 'left' }} key={props.idx}>
                <th scope="row">{props.idx}</th>
                <td title={`trigger action: ${trigger}`}>{props.exchange}</td>
                <td>{props.publisher}</td>
                <td><a href={props.model_url} target="_blank">{props.model_name}</a></td>
                <td className="text-center"><a href={`/subscribers/queue/${props.exchange}`} style={{ textDecoration: 'none' }}>{props.subscriber}</a></td>
                <td>
                    <i className="fa fa-pencil-square-o" aria-hidden="true" style={{ marginRight: '10px', cursor: 'pointer' }}
                        data-bs-toggle="modal" data-bs-target={`#exampleModal${props.id}`} onClick={onClickModal}></i>
                    <Modals
                        id={props.id}
                        // isOpen={isOpen}
                        title={title}
                        body={body}
                        onSubmit={onSubmit} />
                    <i className="fa fa-trash-o" aria-hidden="true" style={{ marginRight: '10px', cursor: 'pointer' }} onClick={onClickDelete}></i>
                </td>
            </tr>
           
        </>
    )
}

export default EventsList