import React, {useState} from 'react'
import { useDispatch } from 'react-redux'
import { deleteSubscriber, putSubscriber } from '../../Redux/Subscriber/actions'
import Modals from '../modals'

const SubscriberList = (props) => {
    const dispatch = useDispatch()

    const [queue, setQueue] = useState(props.queue)
    const [event, setEvent] = useState(props.eventid)
    const [eventField, ] = useState(props.exchange)
    const [bename, setBename] = useState(props.subscriberid)
    const [benameField, ] = useState(props.subscribername)
    const [postaction, setPostaction] = useState(props.postaction)
    const [title, setTitle] = useState("")
    const [body, setBody] = useState("")

    const onChangeQueue = (e) => {
        setQueue(e.target.value)
    }
    const onChangeEvent = (e) => {
        setEvent(e.target.value)
    }
    const onChangeBename = (e) => {
        setBename(e.target.value)
    }
    const onChangePostaction = (e) => {
        setPostaction(e.target.value)
    }

    const add_body = () => {
        return (
            <>
                <form>
                    <div className="mb-3" style={{ textAlign: 'left' }}>
                        <label htmlFor="queue" className="form-label">Queue <span style={{ color: 'red' }}>*</span></label>
                        <input type="text" className="form-control" id="inputEmail3" defaultValue={props.queue} onChange={onChangeQueue} />
                    </div>
                    <div className="row">
                        <div className="col">
                            <div className="mb-3" style={{ textAlign: 'left' }}>
                                <label htmlFor="event" className="form-label">Event <span style={{ color: 'red' }}>*</span></label>
                                <select className="form-select" aria-label="Default select example" defaultValue={event} onChange={onChangeEvent}>
                                    {event ? (
                                        <option value={event} >{eventField}</option>
                                    ) :
                                        (<option disabled selected>--Pilih--</option>)}
                                    {props.dataEvent.map((item, idx) => {
                                        return (
                                            <option value={item.id} key={item.id}>{item.exchange}</option>
                                        )
                                    })}
                                </select>
                            </div>
                        </div>

                        <div className="col">
                            <div className="mb-3" style={{ textAlign: 'left' }}>
                                <label htmlFor="bename" className="form-label">Subscriber Backend <span style={{ color: 'red' }}>*</span></label>
                                <select className="form-select" aria-label="Default select example" defaultValue={bename} onChange={onChangeBename}>
                                    {bename ? (
                                        <option value={bename} >{benameField}</option>
                                    ) :
                                        (<option disabled selected>--Pilih--</option>)}
                                    {props.dataBE.map((item, idx) => {
                                        return (
                                            <option value={item.id} key={item.id}>{item.name}</option>
                                        )
                                    })}
                                </select>
                            </div>
                        </div>
                    </div>

                    
                    
                    <div className="mb-3" style={{ textAlign: 'left' }}>
                        <label htmlFor="post_action" className="form-label">Post Action</label>
                        <input type="text" className="form-control" id="inputEmail3" defaultValue={postaction} onChange={onChangePostaction} />
                    </div>
                </form>

            </>
        )
    }
    const onClickAddModal = () => {
        setTitle("Edit Subscriber")
        setBody(add_body)
    }
    const onSubmit = () => {
        alert('Apakah Anda Yakin ?')
        dispatch(putSubscriber(event, bename, queue, postaction, props.id))
    }
    const onClickDelete = () => {
        alert('Apakah Anda Yakin ?')
        dispatch(deleteSubscriber(props.id))
    }
    return (
        <>
            <tr style={{ textAlign: 'left' }} key={props.idx}>
                <th scope="row">{props.idx}</th>
                <td>{props.exchange}</td>
                <td>{props.queue}</td>
                <td>{props.subscribername}</td>
                <td>
                    <i className="fa fa-pencil-square-o" aria-hidden="true" style={{ marginRight: '10px', cursor: 'pointer' }}
                        data-bs-toggle="modal" data-bs-target={`#exampleModal${props.id}`} onClick={onClickAddModal}></i>
                    <Modals
                        id={props.id}
                        // isOpen={isOpen}
                        title={title}
                        body={body}
                        onSubmit={onSubmit} />
                    <i className="fa fa-trash-o" aria-hidden="true" style={{ marginRight: '10px', cursor: 'pointer' }} onClick={onClickDelete}></i>
                </td>
            </tr>
           
        </>
    )
}

export default SubscriberList