import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Loading from '../Component/loading'
import Modals from '../Component/modals'
import ModelsList from '../Component/models/modelslist'
import { loadModels, postModels } from '../Redux/Models/actions'

const Models = () => {

    const dispatch = useDispatch()
    const data = useSelector((state) => state.models.dataModels);
    const loading = useSelector((state) => state.models.isLoading);


    const [title, setTitle] = useState("")
    const [body, setBody] = useState("")
    const [name, setName] = useState("")
    const [url, setUrl] = useState("")
    const [id,] = useState('addmodels')
    const [updateSearch, setUpdateSearch] = useState("")
    
    useEffect(() => {
        dispatch(loadModels())
    }, [])

    const dataNew = data.filter(
        (item) =>
            item.name.toLowerCase().includes(updateSearch) 
    )

    const list = dataNew.map((item, idx) => {
        return (
            <>
                <ModelsList
                    idx={idx + 1}
                    url={item.url}
                    id={item.id}
                    name={item.name}
                />
            </>
        )
    })
    const add_body = () => {
        return (
            <>
                <form>
                    <div className="row mb-3">
                        <label htmlFor="name" className="col-sm-2 col-form-label">Name <span style={{ color: 'red' }}>*</span></label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" id="inputEmail3" defaultValue={name} onChange={onChangeName} placeholder="Input Name" />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label htmlFor="url" className="col-sm-2 col-form-label">Url <span style={{ color: 'red' }}>*</span></label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" id="inputPassword3" defaultValue={url} onChange={onChangeUrl} placeholder="Input Url" />
                        </div>
                    </div>
                </form>

            </>
        )
    }

    const onChangeName = (e) => {
        setName(e.target.value)
    }
    const onChangeUrl = (e) => {
        setUrl(e.target.value)
    }
    const handleSearchChange = (e) => {
        setUpdateSearch(e.target.value)
    }
    const onClickAddModal = () => {
        setTitle("Create Model")
        setBody(add_body)
    }
    const onSubmit = (e) => {
        e.preventDefault();
        if (name === "") {
            alert("Nama harus diisi!");
        }
        if (url === "") {
            alert("url harus diisi!");
        } else {
            dispatch(postModels(name, url))
        }
    }
    if (loading) {
        return (
            <Loading />
        );
    }
    return (
        <>
            <div style={{ display: 'flex' }}>
                <div className="col-6">
                    <h4 className="mb-2" style={{ textAlign: 'left' }}>List Models</h4>
                </div>
                <div className="col-6" style={{ margin: 'auto', textAlign: 'right' }}>
                    <button type="button" className="btn btn-primary"
                        data-bs-toggle="modal" data-bs-target={`#exampleModal${id}`} onClick={onClickAddModal} style={{ backgroundColor: '#30A4E1' }}>Add Models</button>
                </div>
            </div>
            <hr></hr>
            <div className="mb-3">
                <input
                    type="text"
                    className="form-control"
                    placeholder="Search"
                    onChange={handleSearchChange}
                />
            </div>
            <div className="table-responsive">
                <table className="table table-striped table-hover">
                    <thead>
                        <tr style={{ textAlign: 'left' }}>
                            <th scope="col">#</th>
                            <th scope="col">Model Name</th>
                            <th scope="col">Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {list}
                    </tbody>
                </table>
            </div>
            <Modals
                id={id}
                title={title}
                body={body}
                onSubmit={onSubmit}
            />

        </>
    )
}

export default Models