import axios from 'axios'
import toast from "../../Component/toastr"
import "toastr/build/toastr.min.css"

const URL = `${process.env.REACT_APP_BASE_URL}/api/events`;
const URL_POST = `${process.env.REACT_APP_BASE_URL}/api/event`;

const request = axios.create({
    baseURL: URL,
});

const LoadEventSuccess = (datas) => ({
    type: 'LOAD_EVENTS_SUCCESS',
    datas,
})
const isLoading = (data) => ({
    type: 'EVENT_LOADING',
    data,
});
const LoadEventSearchSuccess = (datas) => ({
    type: 'LOAD_EVENTS_SEARCH_SUCCESS',
    datas,
})
const postEventSuccess = (datas) => ({
    type: 'POST_EVENTS_SUCCESS',
    datas,
})
const putEventSuccess = (datas) => ({
    type: 'EDIT_EVENTS_SUCCESS',
    datas,
})
const deleteEventSuccess = (datas) => ({
    type: 'DELETE_EVENTS_SUCCESS',
    datas,
})
export const loadEvent = () => {
    return (dispatch) => {
        dispatch(isLoading(true))
        return request.get(URL, {})
            .then(function (response) {
                dispatch(LoadEventSuccess(response.data.data.events));
            })
            .catch(function (error) {
                console.error(error);
            })
            .finally(() => {
                setTimeout(function () { dispatch(isLoading(false)); }, 1000);
            });
    };
};

export const loadEventSearch = (param) => {
    return (dispatch) => {
        return request.get(`${process.env.REACT_APP_BASE_URL}/api/events?q=${param}`, {})
            .then(function (response) {
                dispatch(LoadEventSearchSuccess(response.data.data.events));
            })
            .catch(function (error) {
                console.error(error);
                if (error.response.status === 400) {
                    toast("error", "Bad Request", "Data Salah/Tidak Ditemukan", "")
                    setTimeout(() => {
                        window.location.reload()
                    }, 2000);
                }
                else if (error.response.status === 500) {
                    toast("error", "Internal Server Error", "Something Wrong", "")
                    setTimeout(() => {
                        window.location.reload()
                    }, 2000);
                }
                else if (error.response.status === 403) {
                    toast("error", "Acces Forbidden", "FOrbidden Access", "")
                    setTimeout(() => {
                        window.location.reload()
                    }, 2000);
                }
            });
    };
};
export const postEvent = (pubId, modelId, exc, trigger, endpoint, desc, sample_message) => {
    const payload = {
        _backend_publisher_id: pubId,
        _model_id: modelId,
        exchange: exc,
        trigger_action: trigger,
        endpoints: endpoint,
        description: desc,
        sample_message: sample_message
    }
    return (dispatch) => {
        return request.post(URL_POST, payload)
            .then(function (response) {
                dispatch(postEventSuccess(response.data.data.events));
                if (response) {
                    toast("success", "Event Berhasil Ditambahkan", "", "")
                    setTimeout(() => {
                        window.location.reload()
                    }, 2000);
                }
            })
            .catch(function (error) {
                console.error(error);
                if (error.response.status === 400) {
                    toast("error", "Bad Request", "Data Salah/Tidak Ditemukan", "")
                    setTimeout(() => {
                        window.location.reload()
                    }, 2000);
                }
                else if (error.response.status === 500) {
                    toast("error", "Internal Server Error", "Something Wrong", "")
                    setTimeout(() => {
                        window.location.reload()
                    }, 2000);
                }
                else if (error.response.status === 403) {
                    toast("error", "Acces Forbidden", "FOrbidden Access", "")
                    setTimeout(() => {
                        window.location.reload()
                    }, 2000);
                }
            });
    };
};
