import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import EventsList from '../Component/events/eventslist'
import Loading from '../Component/loading'
import Modals from '../Component/modals'
import { loadBackend } from '../Redux/Backend/actions'
import { loadEvent, loadEventSearch, postEvent } from '../Redux/Events/actions'
import { loadModels } from '../Redux/Models/actions'

const Event = (props) => {
    const abc = window.location.href;
    const def = abc.trim();
    const dataExc = def.split("/")[5];

    const dispatch = useDispatch()
    const [bename, setBeName] = useState("")
    const [model, setModel] = useState("")
    const [exchange, setExchange] = useState("")
    const [trigger, setTrigger] = useState("")
    const [endpoint, setEndpoint] = useState("")
    const [description, setDescription] = useState("")
    const [sample_message, setSampleMessage] = useState("")
    const [title, setTitle] = useState("")
    const [body, setBody] = useState("")
    const [id,] = useState("")
    const [search, setSearch] = useState("")
    const [updateSearch, setUpdateSearch] = useState("")

    const handleSearchChange = (e) => {
        setUpdateSearch(e.target.value)
    }
    const onChangeBename = (e) => {
        setBeName(e.target.value)
    }
    const onChangeModel = (e) => {
        setModel(e.target.value)
    }
    const onChangeExchange = (e) => {
        setExchange(e.target.value)
    }
    const onChangeTrigger = (e) => {
        setTrigger(e.target.value)
    }
    const onChangeEndpoint = (e) => {
        setEndpoint(e.target.value)
    }
    const onChangeDescription = (e) => {
        setDescription(e.target.value)
    }
    const onChangeSampleMessage = (e) => {
        setSampleMessage(e.target.value)
    }
    useEffect(() => {
        dispatch(loadEvent())
        dispatch(loadBackend())
        dispatch(loadModels())
        if (dataExc) {
            dispatch(loadEventSearch(dataExc))
        }
    }, [dataExc])
    const data = useSelector((state) => state.events.dataEvent);
    const loading = useSelector((state) => state.events.isLoading);
    const dataSearch = useSelector((state) => state.events.dataEventSearch);
    const dataBE = useSelector((state) => state.backend.dataBackend)
    const dataMdl = useSelector((state) => state.models.dataModels)

    const add_body = () => {
        return (
            <>
                <form>
                    <div className="mb-3" style={{ textAlign: 'left' }}>
                        <label htmlFor="exchange" className="form-label">Exchange <span style={{ color: 'red' }}>*</span></label>
                        <input type="text" className="form-control" id="inputEmail3" defaultValue={exchange} onChange={onChangeExchange} placeholder="input exchange" />
                    </div>

                    <div className="row">
                        <div className="col">
                            <div className="mb-3" style={{ textAlign: 'left' }}>
                                <label htmlFor="backend" className="form-label">Backend Name <span style={{ color: 'red' }}>*</span></label>
                                <select className="form-select" aria-label="Default select example" onChange={onChangeBename}>
                                    <option disabled selected>--Pilih--</option>
                                    {dataBE.map((item, idx) => {
                                        return (
                                            <option value={item.id} key={item.id}>{item.name}</option>
                                        )
                                    })}
                                </select>
                            </div>
                        </div>

                        <div className="col">
                            <div className="mb-3" style={{ textAlign: 'left' }}>
                                <label htmlFor="model" className="form-label">Model <span style={{ color: 'red' }}>*</span></label>
                                <select className="form-select" aria-label="Default select example" onChange={onChangeModel}>
                                    <option disabled selected>--Pilih--</option>
                                    {dataMdl.map((item, idx) => {
                                        return (
                                            <option value={item.id} key={item.id}>{item.name}</option>
                                        )
                                    })}
                                </select>
                            </div>
                        </div>
                    </div>

                    <div className="row">
                        <div className="col">
                            <div className="mb-3" style={{ textAlign: 'left' }}>
                                <label htmlFor="triger" className="form-label">Triger Action </label>
                                <input type="text" className="form-control" id="inputEmail3" defaultValue={trigger} onChange={onChangeTrigger} placeholder="input trigger" />
                            </div>
                        </div>

                        <div className="col">
                            <div className="mb-3" style={{ textAlign: 'left' }}>
                                <label htmlFor="endpoint" className="form-label">Endpoint</label>
                                <input type="text" className="form-control" id="exampleFormControlInput1" defaultValue={endpoint} placeholder="input Url"
                                    onChange={onChangeEndpoint} />
                            </div>
                        </div>
                    </div>
                    
                    <div className="row">
                        <div className="col">
                            <div className="mb-3" style={{ textAlign: 'left' }}>
                                <label htmlFor="exampleFormControlTextarea1" className="form-label">Sample JSON Message</label>
                                <textarea className="form-control" id="exampleFormControlTextarea2" defaultValue={sample_message} rows="8" onChange={onChangeSampleMessage}></textarea>
                            </div>
                        </div>

                        <div className="col">
                            <div className="mb-3" style={{ textAlign: 'left' }}>
                                <label htmlFor="exampleFormControlTextarea1" className="form-label">Description</label>
                                <textarea className="form-control" id="exampleFormControlTextarea1" defaultValue={description} rows="8" onChange={onChangeDescription}></textarea>
                            </div>
                        </div>
                    </div>                   
                </form>

            </>
        )
    }

    useEffect(() => {
        if (dataExc) {
            setSearch(
                dataSearch && dataSearch.map((item, idx) => {
                    return (
                        <>
                            <EventsList
                                idx={idx + 1}
                                id={item.id}
                                exchange={item.exchange}
                                pubId={item._backend_publisher_id}
                                publisher={item._backend_publisher_name}
                                model_id={item._model_id}
                                model_name={item._model_name}
                                model_url={item._model_url}
                                trigger={item.trigger_action}
                                description={item.description}
                                sample_message={item.sample_message}
                                subscriber={item.total_subscribers}
                                endpoint={item.endpoints}
                                dataBE={dataBE}
                                dataMdl={dataMdl}
                            />
                        </>
                    )
                })
            )
        }

    }, [dataExc, dataSearch])

    const dataNew = data.filter(
        (item) =>
            item._backend_publisher_name.toLowerCase().includes(updateSearch) ||
            item._model_name.toLowerCase().includes(updateSearch) ||
            item._backend_publisher_name.toLowerCase().includes(updateSearch) ||
            item.exchange.toLowerCase().includes(updateSearch)

    )
    const list = dataNew.map((item, idx) => {
        return (
            <>
                <EventsList
                    idx={idx + 1}
                    id={item.id}
                    exchange={item.exchange}
                    pubId={item._backend_publisher_id}
                    publisher={item._backend_publisher_name}
                    model_id={item._model_id}
                    model_name={item._model_name}
                    model_url={item._model_url}
                    trigger={item.trigger_action}
                    description={item.description}
                    sample_message={item.sample_message}
                    subscriber={item.total_subscribers}
                    endpoint={item.endpoints}
                    dataBE={dataBE}
                    dataMdl={dataMdl}
                />
            </>
        )
    })

    const onClickAddModal = () => {
        setTitle("Add Event")
        setBody(add_body)
    }

    const onSubmit = (e) => {
        e.preventDefault()
        if (bename === "") {
            alert("Backend name harus diisi!");
        }
        if (model === "") {
            alert("Model harus diisi!");
        }
        if (exchange === "") {
            alert("Exchange harus diisi!");
        } else {
            dispatch(postEvent(bename, model, exchange, trigger, endpoint, description, sample_message))
        }
    }
    if (loading) {
        return (
            <Loading />
        );
    }
    return (
        <>
            <div style={{ display: 'flex' }}>
                <div className="col-6">
                    <h4 className="mb-2" style={{ textAlign: 'left' }}>List Event/Exchange</h4>
                </div>
                <div className="col-6" style={{ margin: 'auto', textAlign: 'right' }}>
                    <button type="button" className="btn btn-primary"
                        data-bs-toggle="modal" data-bs-target={`#exampleModal${id}`} onClick={onClickAddModal} style={{ backgroundColor: '#30A4E1' }}>Add Event</button>
                </div>
            </div>
            <hr></hr>
            <div className="mb-3">
                <input
                    type="text"
                    className="form-control"
                    placeholder="Search"
                    onChange={handleSearchChange}
                />
            </div>
            <div className="table-responsive">
                <table className="table table-striped table-hover">
                    <thead>
                        <tr style={{ textAlign: 'left' }}>
                            <th scope="col">#</th>
                            <th scope="col">Exchange</th>
                            <th scope="col">Backend Name</th>
                            <th scope="col">Model Name</th>
                            <th className="text-center" scope="col">Total Subscriber</th>
                            <th scope="col">Actions</th>

                        </tr>
                    </thead>
                    <tbody>
                        {dataExc !== undefined ? search : list}
                    </tbody>
                </table>
            </div>
            <Modals
                id={id}
                title={title}
                body={body}
                onSubmit={onSubmit} />
        </>
    )
}

export default Event