import React from 'react'
import { Link } from 'react-router-dom'
import ThemeRoutes from "../../Routes/routers";
import './sidebar.css'
const Sidebar = (props) => {
    const activeRoute = props.location.pathname
    return (
        <>
            <div className="card" style={{ border: 'none', position: 'fixed' }}>
                <div className="logo">
                    <img src="https://halalpedia.oss-ap-southeast-5.aliyuncs.com/2020/05/20200508141144-5eb50630deaf4-3desktoplogo.jpg" className="img-responsive center-block" alt="Logo" />
                </div>
                <br></br>
                <div className="list-group" id="list-tab" role="tablist">
                    <a className="list-group-item list-group-item-action disabled" style={{ backgroundColor: '#46AE49', color: 'white' }}>MENU</a>
                    {ThemeRoutes.map((item, idx) => {
                        return (
                            <Link to={item.path} key={idx} className={`list-group-item list-group-item-action ${activeRoute === item.path ? "actived" : ""} pl-5 mr-5 ml-5`} role="tab" aria-controls={item.name} style={{ textDecoration: 'none', }}>
                                {item.name}
                            </Link>
                        )
                    })}
                </div>

            </div>
        </>
    )
}

export default Sidebar