import { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import BackendList from '../Component/backend/backendlist'
import Loading from '../Component/loading'
import Modals from '../Component/modals'
import { loadBackend, postBackend } from '../Redux/Backend/actions'

const Backend = (props) => {
    const dispatch = useDispatch()
    const data = useSelector((state) => state.backend.dataBackend);
    const loading = useSelector((state) => state.backend.isLoading);

    const [name, setName] = useState("")
    const [url, setUrl] = useState("")
    const [title, setTitle] = useState("")
    const [body, setBody] = useState("")
    const [id,] = useState('add')
    const [updateSearch, setUpdateSearch] = useState("")
 

    const handleSearchChange = (e) => {
        setUpdateSearch(e.target.value)
    }
    const onChangeName = (e) => {
        setName(e.target.value)
    }
    const onChangeUrl = (e) => {
        setUrl(e.target.value)
    }
    useEffect(() => {
        dispatch(loadBackend())
    }, [])

    const onClickAddModal = () => {
        setTitle("Create Backend")
        setBody(add_body)
    } 

    const onSubmit = (e) => {
        e.preventDefault();
        if (name === "") {
            alert("Nama harus diisi!");
        }
        if (url === "") {
            alert("url harus diisi!");
        } else {
            dispatch(postBackend(name, url))
        }
    }

    
    const add_body = () => {
        return (
            <>
                <form>
                    <div className="row mb-3">
                        <label htmlFor="name" className="col-sm-2 col-form-label">Name <span style={{ color: 'red' }}>*</span></label>
                        <div className="col-sm-10">
                            <input type="name" className="form-control" id="inputEmail3" defaultValue={name} onChange={onChangeName} placeholder="Input Name" />
                            {/* <span>{err && err ? err.name : ""}</span> */}
                        </div>
                    </div>
                    <div className="row mb-3">
                        <label htmlFor="url" className="col-sm-2 col-form-label">Url <span style={{ color: 'red' }}>*</span></label>
                        <div className="col-sm-10">
                            <input type="text" className="form-control" id="inputPassword3" defaultValue={url} onChange={onChangeUrl} placeholder="Input Url" />
                            {/* <span>{err && err ? err.url : ""}</span> */}
                        </div>
                    </div>
                </form>

            </>
        )
    }
    const dataNew = data.filter(
        (item) =>
            item.name.toLowerCase().includes(updateSearch)

    )
    const list = dataNew.map((item, idx) => {
        return (
            <>
                <BackendList
                    idx={idx}
                    key={idx.toString()}
                    id={item.id}
                    name={item.name}
                    total_exchanges={item.total_exchanges}
                    total_queues={item.total_queues}
                    url={item.url} />
            </>
        )
    })
    if (loading) {
        return (
            <Loading />
        );
    }
    return (
        <>
            <div style={{ display: 'flex' }}>
                <div className="col-6">
                    <h4 className="mb-2" style={{ textAlign: 'left' }}>List Backend</h4>
                </div>
                <div className="col-6" style={{ margin: 'auto', textAlign: 'right' }}>
                    <button type="button" className="btn btn-primary"
                        data-bs-toggle="modal" data-bs-target={`#exampleModal${id}`} onClick={onClickAddModal} style={{ backgroundColor: '#30A4E1' }}>Add Backend</button>
                </div>
            </div>
            <hr></hr>
            <div className="mb-3">
                <input
                    type="text"
                    className="form-control"
                    placeholder="Search"
                    onChange={handleSearchChange}
                />
            </div>
            <div className="table-responsive">
                <table className="table table-striped table-hover">
                    <thead>
                        <tr style={{ textAlign: 'left' }}>
                            <th scope="col">#</th>
                            <th scope="col">Backend Name</th>
                            <th className="text-center" scope="col">Total Exchange</th>
                            <th className="text-center" scope="col">Total Queue</th>
                            <th scope="col">Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {list}
                    </tbody>
                </table>
            </div>
            <Modals
                id={id}
                title={title}
                body={body}
                onSubmit={onSubmit} />
        </>
    )
}

export default Backend